import 'rsuite/dist/rsuite.min.css'
import Home from './component/home';
import Information from './component/info';
import Contact from './component/contact';
import 'bootstrap/dist/css/bootstrap.min.css';
import '../src/assets/css/live-resume.css';
import '../src/assets/vendors/@fortawesome/fontawesome-free/css/all.min.css';
import logo from '../src/assets/image/logo.png'
import humburger from '../src/assets/image/hamburger.svg'
import { Divider, Button } from 'rsuite';
import { useAuth0, withAuthenticationRequired } from "@auth0/auth0-react";
import React from 'react';
import { BrowserRouter as Router, Routes, Route, Link, useRoutes } from 'react-router-dom';
import Details from './component/details';
import { useEffect } from 'react';
import { createBrowserHistory } from 'history';
import stock_back from '../src/assets/image/stock_back_1.png';

let history = createBrowserHistory();



const WrapperApp = (props) => {
    let routes = useRoutes([
        { path: "/", excat: true, element: <Information /> },
        { path: "/nse-stock-analysis", excat: true, element: <Information /> },
        { path: "/contact", excat: true, element: <Contact /> },
        { path: "/analysis", excat: true, element: <Home isRefresh={props.isRefresh} result={props.result} onResultChange={props.onResultChange} onAPIResultChange={props.onAPIResultChange} apiResult={props.apiResult} /> },
        { path: "/details/:stockName", excat: true, element: <Details isRefresh={props.isRefresh} result={props.result} onAPIResultChange={props.onAPIResultChange} apiResult={props.apiResult} /> },
    ]);
    return routes;
};

//define background image with lower opacity css
const background = {
    backgroundImage: `url(${stock_back})`,
    backgroundSize: 'cover',
    backgroundPosition: 'center',
    backgroundRepeat: 'no-repeat',
    width: '100%'
}

function App() {
    //check if user is logged in
    //create await constant to get is authenticated
    const { user, isLoading, loginWithRedirect } = useAuth0();
    const [isRefereshedClicked, setIsRefereshedClicked] = React.useState(false);
    const [resultSet, setResultSet] = React.useState([]);
    const [isMenuShow, setIsMenuShow] = React.useState(false);
    const [apiResult, setApiResult] = React.useState([]);

    useEffect(() => {
        if(!isLoading){
            if(!user){
                loginWithRedirect();
            }
        }
    },[isLoading])

    useEffect(() => {
        //if it is window or tab size is less than 768px then hide the menu
        if (window.innerWidth < 768) {
            setIsMenuShow(false);
        } else {
            setIsMenuShow(true);
        }
    }, []);

    return (
        <>  <Router history={history}>
            <header>

               
                <a className="btn btn-white btn-share ml-auto mr-3 ml-md-0 mr-md-auto" href='https://www.youtube.com/@lalitdetroja1979'>
                    <i className="fab fa-youtube"></i> &nbsp;
                    Subscribe us!</a>
                    <nav className={isMenuShow ?"collapsible-nav show":"collapsible-nav"} id="collapsible-nav">
                   
                    <Link to={{pathname:'./'}} className={
                        history.location.pathname === '/' ? "nav-link active" : "nav-link"
                    }>HOME</Link>
                    <Link to="/analysis" className={
                        history.location.pathname === '/analysis' ? "nav-link active" : "nav-link"
                    }>STOCK ANALYSIS</Link>
                    <Link to="/contact" className={
                        history.location.pathname === '/contact' ? "nav-link active" : "nav-link"
                    }>CONTACT</Link>
                    <Button  className="nav-link"  onClick={() => { loginWithRedirect(); }}>Logout</Button>
                 
                </nav>
                 <button class="btn btn-menu-toggle btn-white rounded-circle" data-toggle="collapsible-nav"
                    data-target="collapsible-nav"><img src={humburger} alt="hamburger" onClick={()=>{
                        console.log("clicked");
                        setIsMenuShow(!isMenuShow);
                    }} /></button>
               
            </header>
            <div className="content-wrapper">
                <aside>
                    <div className="profile-img-wrapper">
                        <img src={logo} alt="profile" style={{ height: "100px", width: "100px" }} />
                    </div>
                    <h1 className="profile-name">The Ocean Of Money</h1>
                    <div className="text-center">
                        <span className="badge badge-white badge-pill profile-designation">Stock Market Analysis</span>
                    </div>
                </aside>
                <main style={background} className='p-1'>
                 
                        <WrapperApp isRefresh={isRefereshedClicked} 
                        onResultChange={(responseResult) => {
                            setResultSet(responseResult)
                        }
                        }
                        onAPIResultChange={(responseResult) => {
                            setApiResult(responseResult)
                        }
                        }
                        result={resultSet}
                        apiResult={apiResult}
                         />
                  
                    <Divider />
                    <footer>
                        The Oceam of money @ <a href="https://www.youtube.com/@lalitdetroja1979" target="_blank" rel="noopener noreferrer">Lalit Detroja</a>. All Rights Reserved 2022
                        <p><b>Disclaimer</b> - The investments discussed or recommended in the market analysis, research reports, etc. may not be suitable for all investors. Investors must make their own investment decisions based on their specific investment objectives and financial position and only after consulting such independent advisors as may be necessary.</p>
                    </footer>
                </main>
            </div>
            </Router>
        </>
    );
}

export default App
